<template>
	<view class="page-container" :style="{height: height}">
		<mescroll-uni ref="mescrollRef" height="100%" :down="downOption" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption">
			<slot></slot>
			<view class="bottom-space"></view>
		</mescroll-uni>
	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollMoreItemMixin from "@/components/mescroll-uni/mixins/mescroll-more-item.js";
	
	export default {
		mixins: [MescrollMixin,MescrollMoreItemMixin],
		name: "pageContainer",
		data() {
			return {
				downOption: {
					use: false
				},
				upOption: {
					use: false
				},
				height: "400px"
			}
		},
		created() {
			// 固定页面高度
			this.height = uni.getSystemInfoSync().windowHeight + 'px';
		},
		methods: {
			
		}
	}
</script>

<style lang="scss" scoped>
	.page-container {
		background-color: $uni-bg-color-grey;
		position: relative;
	}
	.page-container::before {
		content: '';
		position: absolute;
		border-top: 1rpx solid #eaeef1;
		-webkit-transform: scaleY(0.5);
		transform: scaleY(0.5);
		top: 0;
		right: 0;
		left: 0;
		z-index: 100;
	}
	.bottom-space {
		height: 100rpx;
	}
</style>
